.anything-box{
    border: 1px solid orange;
    display: inline-block;
    width: 300px;
    height: 200px;
}
.container{
    position: relative;
    margin-top: 12px;
    height: 30px;
    border: solid 1px #d5d5d5;
}
.selectIco{
    position: absolute;
    width: 40px;
    text-align: center;
    color: #999999;
    font-size: 12px;
    right: 0px;
    float: left;
    height: 30px;
    line-height: 30px;
}
.icoZk{
    cursor: pointer;
    font-size: 12px;
    color: #999999;
}
.content{
    position: absolute;
    width: 300px;
    max-height: 200px;
    top: 30px;
    background: #fff;
    overflow-x: hidden;
    border-radius: 2px;
    font-size: 14px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
}