.trigger {
  font-size: 18px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  transition: color 0.3s;
}

.trigger:hover {
  color: #1890ff;
}

.logo {
  height: 32px;
  margin: 16px;
}

.site-layout .site-layout-background {
  background: #fff;
}

.toprev {
  position: absolute;
  content: " ";
  width: 50px;
  height: 80px;
  display: inline-block;
  left: 0;
  top: 50;
  transform: translateY(-50%);
}

.evenColor:hover td {
  background-color: rgba(199, 85, 50, 0.342) !important;
}

.title {
  border: 1px solid #000;
  height: 200px;
}

.overLay {
  border: 2px solid #ff0000;
  left: 100px;
  padding-top: 20px;
}
.overLay .ant-tooltip-arrow{
  top: 90px ;
}
.children {
  display: inline-block;
  border: 1px solid #000;
  height: 100px;
}


.triangle{
  display: inline-block;
  font-size: 0;
  overflow: hidden;
}
 
.triangle:before{
  content: "";
  position: relative;
  display: inline-block;
  border: 25px solid transparent;
}
 
.triangle-up{
  position: relative;
  top: 2px;
  border-top-left-radius: 50%;
  border-top-right-radius: 50%;
}
.triangle-up:before{
  bottom: 2px;
  border-top-width: 0;
  border-bottom-width: 50px;
  border-bottom-color: rgb(181, 181, 181);
}
.triangle-right{
  position: relative;
  right: 2px;
  border-top-right-radius: 50%;
  border-bottom-right-radius: 50%;
}
.triangle-right:before{
  left: 2px;
  border-right-width: 0;
  border-left-width: 50px;
  border-left-color: rgb(181, 181, 181);
}
.triangle-down{
  position: relative;
  bottom: 2px;
  border-bottom-right-radius: 50%;
  border-bottom-left-radius: 50%;
}
.triangle-down:before{
  top: 2px;
  border-bottom-width: 0;
  border-top-width: 50px;
  border-top-color: rgb(181, 181, 181);
}
 
.triangle-left{
  position: relative;
  left: 2px;
  border-top-left-radius: 50%;
  border-bottom-left-radius: 50%;
}
.triangle-left:before{
  right: 2px;
  border-left-width: 0;
  border-right-width: 50px;
  border-right-color: rgba(181, 181, 181, 1);
}